<template>
  <div>
    <el-button
        style="width: 200px"
        type="warning"
        size="small"
        @click="exportDataInfos"

    >
      导出全部查询数据
    </el-button>
    <el-form label-width="120" :model="form">
      <el-row>
        <el-col :span="12">
          <el-form-item
              label="选择条件:"
          >
            <el-radio-group
                v-model="form.query_org"
                @change="getCustomerDetail"
            >
              <el-radio
                  v-for="item in items3"
                  :key="item.key"
                  :label="item.label"
              >
                {{ item.title }}
              </el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>

        <el-col :span="12">
          <el-form-item
              label="选择纬度:"
              @change="cleanQureyData"
          >
            <el-radio v-model="form.query_type" label="1" @change="handler">客户纬度</el-radio>
            <el-radio v-model="form.query_type" label="2" @change="handler">账户纬度</el-radio>

          </el-form-item>
        </el-col>
      </el-row>
      <el-row
          v-if="form.query_type==1"
      >
        <el-col :xl="5" :lg="7" :md="8" :sm="24">
          <el-form-item
              label="活期余额范围"
          >
            <div style="display: flex">
              <el-input size="small" clearable v-model="form.minCurrent" @change="handleMinChange"/>
              ~
              <el-input size="small" clearable v-model="form.maxCurrent" @change="handleMaxChange"/>
            </div>
            <!--            <el-cascader
                            :props="props"
                            v-model="form.currentRange"
                            filterable
                        ></el-cascader>-->
          </el-form-item>
        </el-col>
        <el-col :xl="5" :lg="7" :md="8" :sm="24">
          <el-form-item
              label="定期余额范围"
          >
            <div style="display: flex">
              <el-input size="small" clearable v-model="form.minTermly" @change="handleMinChange"/>
              ~
              <el-input size="small" clearable v-model="form.maxTermly" @change="handleMaxChange"/>
            </div>
          </el-form-item>
        </el-col>
        <el-col :xl="5" :lg="7" :md="8" :sm="24">
          <el-form-item
              label="AUM范围"
          >
            <div style="display: flex">
              <el-input size="small" clearable :min="0" v-model="form.minAUM" @change="handleMinChange"/>
              ~
              <el-input size="small" clearable :min="0" v-model="form.maxAUM" @change="handleMaxChange"/>
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item
              label="客户姓名"
          >
            <div style="display: flex">
              <el-input
                  v-model="form.cusname"
                  placeholder="请输入姓名"
                  clearable
              ></el-input>
            </div>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row
          style="position: relative">
        <el-col :span="12">
          <el-form-item
          >{{ date_type }}
            <el-date-picker
                clearable
                size="small"
                v-if="form.query_type==1"
                v-model="form.query_date"
                type="date"
            >
            </el-date-picker>
            <el-date-picker
                size="small"
                v-else
                v-model="dateRange"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item
              label="查询服务点:"

          >
            <el-select
                v-model="form.tlrclt"
                filterable
                clearable
                size="small"
                placeholder="请输入服务点名称"
            >
              <el-option
                  v-for="item in siteList"
                  :key="item.site_no"
                  :label="item.site_name"
                  :value="item.site_no"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row
          style="position: relative"
      >
        <el-col :span="3">
          <el-form-item label-width="0">
            <el-button
                type="primary"
                size="small"
                @click="getCustomerDetail"
                icon="el-icon-search"
            >查询
            </el-button
            >
          </el-form-item>
        </el-col>
        <div class="cus-scroll">
          <div
              class="cus-scroll-child"
              :style="{
              width: cusScrollWidth,
            }"
          ></div>
        </div>
      </el-row>
    </el-form>
    <div
        class="table"
        ref="table"
    >
      <el-table
          border
          :data="showData"
      >
        <template
            v-for="(
            item, index
          ) in showColumns"
        >
          <el-table-column
              :key="index"
              :label="item.label"
              :prop="item.prop"
              align="center"
              width="160"
          ></el-table-column>
        </template>
      </el-table>
    </div>
    <el-pagination
        style="
        text-align: center;
        margin: 10px 0;
      "
        @size-change="changeSize"
        @current-change="changePage"
        :current-page="form.page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="form.number"
        size="small"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import customer from "./siteMixin/customerdetails";
import account from "./siteMixin/account";

let id = -10000;
export default {

  data() {
    return {
      date_type: "业绩日期",
      form: {
        query_org: "1",
        query_type: "1",
        query_date: new Date(
            new Date().getTime() -
            24 * 60 * 60 * 1000
        ),
        minCurrent: "",
        maxCurrent: "",
        minAUM: "",
        maxAUM: "",
        minTermly: "",
        maxTermly: "",
        page: 1,
        number: 10,
      },
      items3: [
        {
          key: "1",
          label: "1",
          title: "所属机构",
        },
        {
          key: "2",
          label: "2",
          title: "落地支行",
        },
      ],
      account,
      customer,
      total:0,
      showColumns: [],
      showData: [],
      siteList: [],
      cusScrollWidth: 0,
      dateRange: [],
    };
  },
  computed: {
    items() {
      return this.form.query_org == '2' ? itemsList.filter(item => parseInt(item.key) != 3) : JSON.parse(JSON.stringify(itemsList))
    },

  },
  mounted() {
    this.handler()
    this.getSiteSelectList();

  },
  methods: {
    //获取客户明细
    getCustomerDetail() {
      this.showData = [];
      let opt = JSON.parse(
          JSON.stringify(
              this.form
          )
      );
      if (this.form.query_type == 2) {
        delete opt.query_date;
        if (this.dateRange.length>0) {
          opt.minDate = this.formateTime(
              this.dateRange[0]
          );
          opt.maxDate = this.formateTime(
              this.dateRange[1]
          );
        } else {
          opt.minDate = "";
          opt.maxDate = "";
        }
      }
      if (this.form.query_type == 1) {
        opt.query_date = this.formateTime(
            opt.query_date ||
            new Date()
        );
      }
      if (!this.verification()) {
        this.$message.error("请将查询范围填写完整");
        return;
      }
      this.$api
          .getCustomerDetail(opt)
          .then((res) => {
            this.showData = res.data.returnList;
            this.total = res.data.total;
          })
    },
    handleMinChange(value) {
      this.verification()
    },
    handleMaxChange(value) {
      this.verification()
    },
    //字段验证
    verification() {
      if (this.form.minAUM > this.form.maxAUM) {
        this.$message.warning("起始数不能大于结束数");
        return false;
      }
      if (this.form.minCurrent > this.form.maxCurrent) {
        this.$message.warning("起始数不能大于结束数");
        return false;
      }
      if (this.form.minTermly > this.form.maxTermly) {
        this.$message.warning("起始数不能大于结束数");
        return false;
      }
      return true;

    },

    //站点列表
    getSiteSelectList() {
      if (this.form.query_org == '1') {
        //所属机构
        this.$api
            .getSiteSelectList()
            .then((res) => {
              this.siteList =
                  res.data.siteList;
            });
      } else {
        //落地支行
        this.$api
            .getSiteSelectList({
              "zh_flag": "1"
            })
            .then((res) => {
              this.siteList =
                  res.data.siteList;
            });
      }
    },
    handleOverWidth() {
      let tableHeader = document.querySelector(
          ".el-table__header"
      );
      this.cusScrollWidth =
          tableHeader.style.width;
    },
    cleanQureyData() {
      if (
          this.form.query_type != "1"
      ) {
        this.$set(
            this.form,
            "tlrclt",
            null
        );
        this.$set(
            this.form,
            "minTermly",
            null
        );
        this.$set(
            this.form,
            "maxTermly",
            null
        );
        this.$set(
            this.form,
            "minCurrent",
            null
        );
        this.$set(
            this.form,
            "maxCurrent",
            null
        );
        this.$set(
            this.form,
            "minAUM",
            null
        );
        this.$set(
            this.form,
            "maxAUM",
            null
        );
        this.$set(
            this.form,
            "cusname",
            null
        );
      }
      if (
          this.form.query_type != "2"
      ) {
        this.$set(
            this.form,
            "tlrclt",
            null
        );
      }
    },
    formateTime(time) {
      let date = new Date(time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      return (
          y + '' +
          (m < 10 ? "0" + m : m) + '' +
          (d < 10 ? "0" + d : d)
      );
    },
    handler() {
      if (this.form.query_type == 1) {
        this.showColumns = JSON.parse(
            JSON.stringify(
                this.customer
            )
        )
        this.date_type = "业绩日期"
        this.getCustomerDetail()
      } else {
        this.showColumns = JSON.parse(
            JSON.stringify(
                this.account
            )
        )
        this.date_type = "开户日期"
        this.getCustomerDetail()
      }
      this.form.page = 1;
      this.form.number = 10;
      this.cleanQureyData()
    },
    // 导出
    exportDataInfos() {
      //this.loading = true
      let opt = JSON.parse(
          JSON.stringify(
              this.form
          )
      );
      if (this.form.query_type == 2) {
        delete opt.query_date;
        if (this.dateRange.length>0) {
          opt.minDate = this.formateTime(
              this.dateRange[0]
          );
          opt.maxDate = this.formateTime(
              this.dateRange[1]
          );
        } else {
          opt.minDate = "";
          opt.maxDate = "";
        }
      }
      if (this.form.query_type == 1) {
        opt.query_date = this.formateTime(
            opt.query_date ||
            new Date()
        );
      }
      this.$api.exportCustomerDetail(opt).then((res) => {
        //this.loading = false
        var fileName = res.data.fileName;
        var filePath = res.data.filePath;
        if (null == fileName || null == filePath) {
          this.$message({
            message: "导出失败，系统异常",
            type: "error",
          });
          return false;
        }

        var objSelector = document.createElement("form");
        document.body.appendChild(objSelector);
        objSelector.method = "post";
        objSelector.action = "/hnm/txCtrl?txcode=mediaFile";
        //objSelector.action = "http://localhost:7001/hnm/txCtrl?txcode=mediaFile";
        objSelector.enctype = "multipart/form-data";
        var input01 = document.createElement("input");
        var input02 = document.createElement("input");
        var input03 = document.createElement("input");
        var input04 = document.createElement("input");
        input01.setAttribute("name", "filePath");
        input01.setAttribute("type", "hidden");
        input01.setAttribute("value", filePath);
        input02.setAttribute("name", "fileName");
        input02.setAttribute("type", "hidden");
        input02.setAttribute("value", fileName);
        input03.setAttribute("name", "isDownload");
        input03.setAttribute("type", "hidden");
        input03.setAttribute("value", "1");
        input04.setAttribute("name", "isDelete");
        input04.setAttribute("type", "hidden");
        input04.setAttribute("value", "1");
        objSelector.appendChild(input01);
        objSelector.appendChild(input02);
        objSelector.appendChild(input03);
        objSelector.appendChild(input04);
        objSelector.submit();

        this.$message({
          message: "导出成功",
          type: "success",
        });
      });
    },
    // 修改页面大小
    changeSize(size) {
      this.form.number = size;
      this.getCustomerDetail();
    },
    // 跳转页码
    changePage(page) {
      this.form.page = page;
      this.getCustomerDetail();
    },
  },
  watch: {
    data: {
      handler() {
        this.showData = JSON.parse(
            JSON.stringify(this.data)
        );
        this.$nextTick(() => {
          this.handleOverWidth();
        });
      },
      deep: true,
    },

  }
  ,
}


</script>

<style lang="scss" scoped>
.deposit {
  display: flex;
  flex-flow: column;
  height: 100%;
  position: relative;

  &-table {
    flex: 1;
  }

  .cus-scroll {
    width: calc(100%);
    overflow-x: scroll;
    height: 20px;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 38px;

    &-child {
      height: 10px;
      background: white;
    }
  }

  .cus-scroll::-webkit-scrollbar {
    height: 20px;
  }

  .cus-scroll::-webkit-scrollbar-track {
    height: 20px;
  }
}
</style>


